import React , {Component} from 'react'
import BaseTable from '@/components/BaseTable'
import BaseContent from '@/components/BaseContent'
import {
    Button,
    Modal,
    Form,
    Input
} from 'antd'
import { connect } from 'dva'

const mapStateToProps = ({room}) => {
    return {...room}
}
@connect(mapStateToProps)

@Form.create()

class Room extends Component{

    state = {
        visible: false
    }

    componentDidMount() {
        let {
            dispatch
        } = this.props;

        dispatch({
            type: 'room/getAllRoom'
        })
    }

    getColumns = () => {
        const columns = [{
            title: '教室号',
            dataIndex: 'room_text',
            key: 'room_id',
        }, {
            title: '操作',
            render: (record) => {
                return <span onClick={() => this.delRoom(record)}>删除</span>
            }
        }];
        return  columns
    }

    addRoom = () => {
        this.setState({
            visible: true
        });
    }

    delRoom = ({room_id}) => {
        Modal.confirm({
            content: '确定要删除此教室吗？',
            onOk: () => {
                let {
                    dispatch
                } = this.props;
                dispatch({
                    type: 'room/delRoom',
                    payload: {
                        room_id
                    }
                })
            }
        })
        
    }

    handleOk = () => {
        let { validateFields, resetFields } = this.props.form;
        let {
            dispatch
        } = this.props;
         validateFields((err, values) => {
             if (err) {
                 return
             }
            dispatch({
                type: 'room/addRoom',
                payload: {
                    room_text: values.room
                }
            })
            this.setState({
                visible: false
            })
            resetFields();
         })
    }

    handleCancel = () => {
        this.setState({
            visible: false
        })
    }

    render(){
        let {
            allRoom,
            form: {getFieldDecorator}
        } = this.props;
        let {
            visible
        } = this.state
        return <BaseContent>
            <BaseTable
                rowKey={record => record.room_id}
                dataSource={allRoom}
                columns={this.getColumns()}
                pagination={false}
                buttons={[
                    <Button
                        type="primary"
                        icon="plus"
                        size="large"
                        style={{float: 'right'}}
                        onClick={this.addRoom}
                    >添加教室</Button>
                ]}
                wrapStyle={{
                    padding: 0,
                }}
            />
            <Modal
                visible={visible}
                title="添加班级"
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                footer={[
                    <Button key="back" onClick={this.handleCancel}>取消</Button>,
                    <Button key="submit" type="primary" onClick={this.handleOk}>
                        提交
                    </Button>,
                ]}
            >
                <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }}>
                    <Form.Item
                        label="教室号"
                    >
                        {getFieldDecorator('room', {
                            rules: [{ required: true, message: '请输入教室号' }],
                        })(
                            <Input type="text" placeholder="教室名" />
                        )}
                    </Form.Item>
                </Form>
            </Modal>
        </BaseContent>
    }
}
export default Room;